import * as React from 'react';
import cx from 'classnames';
import styles from './Footer.module.scss';
import useIntlHelper from '~src/i18n/helpers/useIntlHelper';
import SocialLinks from '../Navigation/Items/SocialLinks';

const Footer = () => {
	const { formatMessage, intl } = useIntlHelper();
	return (
		<footer className={cx(styles.siteFooter, `outer`)}>
			<div className={cx(styles.siteFooterContent, `inner`)}>
				<section className="copyright">
					{formatMessage('meta.title')} {formatMessage('footer.copyright')}
					{intl.formatDate(new Date(2010, 1, 1), { year: 'numeric' })}-
					{intl.formatDate(new Date(), { year: 'numeric' })}
				</section>
				<nav className={styles.siteFooterNav}>
					<SocialLinks />
				</nav>
			</div>
		</footer>
	);
};

export default Footer;
