import * as React from 'react';
//import * as PropTypes from 'prop-types';
import { useLocaleContext } from '~src/context/Hooks/useLocaleContext';
import useIntlHelper from '~src/i18n/helpers/useIntlHelper';
import Link from '~src/i18n/components/Link';
import styles from '../Navigation.module.scss';

const navigation = [
	{
		label: 'navigation.home',
		url: '/',
	},
	{
		label: 'navigation.artworks',
		url: '/artworks/',
	},
	{
		label: 'navigation.blog',
		url: '/blog/',
	},
	{
		label: 'navigation.films',
		url: '/filmography/',
	},
	/* 	{
		label: 'navigation.tutorials',
		url: '/tutorials',
	}, */
	{
		label: 'navigation.about',
		url: '/about',
	},
];
/**
 * Navigation component
 *
 * The Navigation component takes an array of your Ghost
 * navigation property that is fetched from the settings.
 * It differentiates between absolute (external) and relative link (internal).
 * You can pass it a custom class for your own styles, but it will always fallback
 * to a `site-nav-item` class.
 *
 */
const Navigation = ({ navClass }) => {
	const { formatMessage } = useIntlHelper();

	//const [locale] = useLocale();
	const { activeLocale } = useLocaleContext();
	const locale = activeLocale;
	const items = [];

	navigation.map((navItem: { url: string; label: string }, i: React.Key) => {
		if (navItem.url.match(/^\s?http(s?)/gi)) {
			items.push(
				<li key={i} className={`nav-${navItem.label.toLowerCase()}`} role="menuitem">
					<a className={navClass} href={navItem.url} target="_blank" rel="noopener noreferrer">
						{navItem.label}
					</a>
				</li>,
			);
		} else {
			items.push(
				<li key={i} className={`nav-${navItem.label.toLowerCase()}`} role="menuitem">
					<Link className={navClass} to={navItem.url}>
						{formatMessage(navItem.label)}
					</Link>
				</li>,
			);
		}
	});

	return (
		<ul className={styles.nav} role="menu">
			{items}
			<li id="locale-switch" className={null} role="menuitem">
				<Link locale={locale === 'en' ? 'fa' : 'en'} to="/">
					{locale === 'en' ? 'فارسی' : 'English'}
				</Link>
			</li>
		</ul>
	);
};

Navigation.defaultProps = {
	navClass: `nav-home nav-current`,
};

