import * as React from 'react';
import LocalizedLink from '~src/i18n/components/localizedLink';
//import {Link }from 'gatsby';

const Link = process.env.STORYBOOK_ENV
	? ({ children, to, ...props }) => (
			<a href={to} {...props}>
				{children}
			</a>
	  )
	: LocalizedLink;

export default Link;
